















import Vue from 'vue'
export default Vue.extend({
    name: "Error404",
    props: ['lan', 'prof'],
    computed:{
        BackText(){
            if (this.lan == 'ru')
                return 'Вернуться на главную страницу...'
            else
                return 'Back to home page...'
        },
        ErrorText(){
            if (this.lan == 'ru')
                return 'Ошибка404 - страница не найдена'
            else
                return 'Error404 - page not found'
        }
    }
    
})
