










import Vue from 'vue'
export default Vue.extend({
    name: "VideoFrame",
    props: {
      Link: String,
      proportion: Number
    },
    computed:{
      Proportion(){
        if(this.proportion == undefined || this.proportion == null || isNaN(parseInt(String(this.proportion))))
          return String(9/16*100);
        else
          return String(1/this.proportion*100);
      }
    }
    
})
